.gpt3__cta{
    
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
}
.gpt3__cta-content p{
font-size: 12px;
font-weight: 500;
line-height: 30px;
text-align: left;
font-family: var(--font-family);
color: #0E0E0E;
    
}
.gpt3__cta-content h3{
    font-family: var(--font-family);
font-size: 24px;
font-weight: 800;
line-height: 45px;
text-align: left;

}
.gpt3__cta-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}
.gpt3__cta-btn button{
background: #000000;
border-radius: 40px ;
font-family: var(--font-family);
font-size: 18px;
font-weight: bold;
line-height: 30px;
color: #FFFFFF;
padding: 0.5rem 1rem;
border: none;
outline: none;
cursor: pointer;
min-width: 150px;

}
@media screen and (max-width: 650px){
    .gpt3__cta{
        flex-direction: column;
    }
    
    .gpt3__cta-btn{
        margin:2rem 0;
    }
    
}
@media screen and (max-width: 500px){
    .gpt3__cta{
        margin: 4rem 2rem;
    }
    .gpt3__cta-content h3{
        font-size: 18px;
        line-height: 32px;
    }
    .gpt3__cta-btn button{
        font-size: 14px;
        line-height: 28px;
    }
    
}
